import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ExpenseServiceService } from 'src/app/core/services/expense-service.service';

@Component({
  selector: 'app-expense-view',
  templateUrl: './expense-view.component.html',
  styleUrls: ['./expense-view.component.scss']
})
export class ExpenseViewComponent implements OnInit {
  public expenseId:any=""
  public expenseDataId:any;
  public expensecategoryname:any
  public expensedate:any;
  public note:any;
  public paymentMode:any;
  public expenseType:any;
  public totalamount:any;
  public expenseitems:any;

  constructor(
   private expenseservice: ExpenseServiceService,@Inject(MAT_DIALOG_DATA) public expenseData: any,public dialogRef: MatDialogRef<ExpenseViewComponent>
  ) { }

  ngOnInit(): void {
    this.expenseId = this.expenseData.expenseId;
    this.getExpenseById();
  }
  getExpenseById(){
    this.expenseservice.GetExpenseBYId({},this.expenseId).then((res:any)=>{
     
     this.expensecategoryname=res.data.expensecategory.name;
     this.expensedate=res.data.date;
     this.paymentMode=res.data.paymentMode;
     this.expenseType=res.data.expenseType;
     this.totalamount=res.data.totalAmount
     this.note =res.data.note;

     this.expenseitems=res.data.expenseitems;
     
    })
    

  }
  cancel(){
    this.dialogRef.close();
  }

}
